import {
  AfterViewInit,
  Component,
  ElementRef,
  OnInit,
  ViewChild
} from '@angular/core';
import { DomSanitizer, SafeResourceUrl } from '@angular/platform-browser';
import { ActivatedRoute, Router } from '@angular/router';
import { fromEvent, skip } from 'rxjs';
import { WindowActionService } from '../../shared/service/window-action.service';

@Component({
  selector: 'campaign',
  templateUrl: './campaign.component.html'
})
export class CampaignComponent implements OnInit, AfterViewInit {
  url: SafeResourceUrl = '';
  title = 'Campaign';
  @ViewChild('myIframe', { static: false })
  myIframe!: ElementRef<HTMLIFrameElement>;

  constructor(
    private domSanitizer: DomSanitizer,
    private activatedRoute: ActivatedRoute,
    private windowActionService: WindowActionService,
    private router: Router
  ) {}

  ngOnInit(): void {
    const url = this.activatedRoute.snapshot?.queryParamMap.get('url') ?? '';
    this.title = this.activatedRoute.snapshot?.queryParamMap.get('title') ?? '';
    this.url = this.domSanitizer.bypassSecurityTrustResourceUrl(url);
  }

  ngAfterViewInit(): void {
    fromEvent(this.myIframe.nativeElement, 'load')
      .pipe(skip(1))
      .subscribe(() => {
        const iframe = this.myIframe.nativeElement;
        const iframeWindow = iframe.contentWindow;
        const location = iframeWindow?.location.href;
        const pathName = iframeWindow?.location.pathname ?? '';
        const param = iframeWindow?.location.search ?? '';
        const checkHost =
          location?.indexOf(this.windowActionService.getHostName()) ?? -1;
        if (checkHost > -1) {
          this.router.navigate([`/${pathName}${param}`], {
            replaceUrl: true
          });
        }
      });
  }
}
